import React, {useState} from 'react';
import Header from '../header';
import styles from './calendar.module.scss';
import MonthlyCalendarHeader from './MonthlyCalendarHeader';
import MonthlyCalendarContent from './MonthlyCalendarContent';
// import './calendar.module.scss';

function Calendar(props) {
    const [today, setToday] = useState(new Date());
    const [monthStartOn, setMonthStartOn] = useState(true);
    const [curMonth, setCurMonth] = useState(new Date(today.getFullYear(), today.getMonth(), 1));

    function changeMonth(e, changeVal) {
        setCurMonth(new Date(curMonth.setMonth(curMonth.getMonth()+changeVal)));
    }
    function monthStartUpdate() {
        setMonthStartOn(!monthStartOn);
    }

    return (    
        <>  
            <Header number={props.number} />
            <div className={styles['App-container']}>
                <div className={styles["monthlyCalendar"]}>
                    <MonthlyCalendarHeader curMonth={curMonth} changeMonth={changeMonth}/>
                    <MonthlyCalendarContent curMonth={curMonth} startWithMon={(monthStartOn)?'0':'1'}/>
                    <label htmlFor="monthStart">
                        <input type="checkbox" name="monthStart" id="monthStart" checked={monthStartOn} onChange={monthStartUpdate} />
                        Month starts on Sunday.
                    </label>
                </div>
            </div>
        </>
    );   
    
}
  

  
export default Calendar;
