import React, {useState, useEffect} from 'react';
import Header from '../header';
import styles from './filter.module.scss';
import axios from "axios";

function Filter(props) {
    const [countries, setCountries] = useState([]);
    const [loading, setLoading] = useState(false);
    const [search, setSearch] = useState("");
    const [filteredCountries, setFilteredCountries] = useState([]);

    useEffect(() => {
        setLoading(true);
        axios
            .get("https://restcountries.eu/rest/v2/all")
            .then((res) => {
                setCountries(res.data);
                setLoading(false);
            })
            .catch((err) => {
                console.log(err);
            });
    }, []);

    useEffect(() => {
        setFilteredCountries(
            countries.filter((country) =>
                country.name.toLowerCase().includes(search.toLowerCase())
            )
        );
    }, [search, countries]);

    if (loading) {
        return(
            <>
                <Header number={props.number} />
                <div className={styles['App-container']}>
                    <div className={styles['filter-container']}>
                        <h1 style={{textAlign: 'center'}}>Countries Lists</h1>
                        <input
                            type="text"
                            placeholder="Search Countries"
                            onChange={(e) => setSearch(e.target.value)}
                            className={styles['input']}
                        />
                        <p>Loading countries...</p>
                    </div>
                </div>
            </>
        ) 
    }
    if (!loading) {
        return (
            <>
                <Header number={props.number} />
                <div className={styles['App-container']}>
                    <div className={styles['filter-container']}>
                        <h1 style={{textAlign: 'center'}}>Countries Lists</h1>
                        <input
                            type="text"
                            placeholder="Search Countries"
                            onChange={(e) => setSearch(e.target.value)}
                            className={styles['input']}
                        />
                        <div className={styles["country-container"]}>
                            {filteredCountries.map((country, idx) => (
                                <div className={styles["country-item"]} key={idx}>
                                    <div className={styles["country"]}>
                                        <span>
                                            <img src={country.flag} alt={country.name} style={{ width: "40px", height: "40px", marginRight: "10px" }} />
                                        </span>
                                        <span>
                                            <div>{country.name}</div>
                                        </span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </>
        );
    }
}
export default Filter;
