import React, {useState} from 'react';
import styles from './calendar.module.scss';

function MonthlyCalendarHeader(props){
    const [curMonth, setCurMonth] = useState(props.Month);
    const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    return (
        <div className={styles["monthlyCalendarHeader"]}>
            <div className={styles["monthlyCalendarYear"]}>{props.curMonth.getFullYear()}</div>
            <div className={styles["monthlyCalendarMonth"]}>
            <span onClick={()=>props.changeMonth(this, -1)} className={styles["btnChangeMonth"]}>&lt;</span>
            <span>{monthNames[props.curMonth.getMonth()]}</span>
            <span onClick={()=>props.changeMonth(this, 1)} className={styles["btnChangeMonth"]}>&gt;</span>
            </div>
        </div>
    )
}

  
export default MonthlyCalendarHeader;
