import React from 'react';
import styles from './calendar.module.scss';
function MonthlyCalendarContent(props) {
   
    var calendarClassName = "";
    var content = [];
    var yyyyMM = `${props.curMonth.getFullYear()}${props.curMonth.getMonth()}`;
    var firstDayStartAt = props.curMonth.getDay() - props.startWithMon;
    var lastDayOfMonth = (new Date(props.curMonth.getFullYear(), props.curMonth.getMonth()+1, 0));
    var lastDayLeft = 7 - (lastDayOfMonth.getDay()+1);
    for(var i=0; i<firstDayStartAt; i++) {
      content.push(<div key={`empty1_${yyyyMM}${i}`} className={styles["dayCell"]}></div>);
    }

    for(var i=1; i<=lastDayOfMonth.getDate(); i++) {
      content.push(<div key={`date_${yyyyMM}${i}`} className={styles["dayCell"]}>{i}</div>);
    }
    if(props.startWithMon=="1") {
      calendarClassName = "startWithMon";
    } else {
      calendarClassName = "startWithSun";
    }
    return <div className={styles["calendar_content"]}>
      <MonthlyCalendarContentHeader startWithMon={props.startWithMon}/>
      <div className={styles[calendarClassName]}>
        {content}
      </div>
    </div>    
}
function MonthlyCalendarContentHeader(props) { 
  if(props.startWithMon=="1") {
      return (
          <div className={styles["startWithMon"]}>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>M</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>T</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>W</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>T</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>F</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>S</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>S</div> 
          </div>
      );
  } else {
      return (
          <div className={styles["startWithSun"]}>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>S</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>M</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>T</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>W</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>T</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>F</div>
              <div className={`${styles["dayCell"]} ${styles["cellHeader"]}`}>S</div>
          </div>
      );
  }
}
export default MonthlyCalendarContent;